.projects h2 {
  margin-top: 0;
  margin-bottom: 35px;
}

.projects__list {
  display: grid;
  grid-template-columns: 608px 608px;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;

  @media (max-width: $desktop-width) {
    grid-template-columns: 504px 504px;
  }

  @media (max-width: $tablet-width) {
    grid-template-columns: 504px;
  }

  @media (max-width: $mobile-width) {
    grid-template-columns: 403px;
  }

  @media (max-width: 524px) {
    grid-template-columns: 310px;
  }
}

.projects__list li {
  overflow: hidden;
  position: relative;
}

.projects__list a {
  display: block;
  text-decoration: none;

  &:hover {
    box-shadow: 0 0 6px 2px rgba($background-color, 0.6);
  }

  &:hover .projects__info {
    transform: translateY(0%);
  }

  &:hover img {
    transform: scale(1.1);
  }

  @media (max-width: $mobile-width) {
    position: relative;
  }
}

.projects__list img {
  border-radius: 10px;
  transition: transform 0.5s;

  @media (max-width: $desktop-width) {
    width: 504px;
    height: 274px;
  }

  @media (max-width: $mobile-width) {
    width: 403px;
    height: 174px;
  }

  @media (max-width: 524px) {
    width: 310px;
    height: 139px;
  }
}

.projects__info {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: auto;
  padding: 20px;
  text-align: center;
  font-size: 18px;
  line-height: 23px;
  color: $light-gray;
  background-color: rgba($black-basic, 0.8);
  text-shadow: 0px 2px 4px $black-basic;
  transition: transform 0.5s;
  transform: translateY(100%);

  @media (max-width: $mobile-width) {
    top: 80px;
    padding-top: 0px;
    padding-right: 15px;
    padding-left: 15px;
    color: $white-basic;
    font-size: 14px;
    line-height: 18px;
    background: linear-gradient(180deg, rgba($background-color, 0.62) 0%,
      rgba($background-color, 0.5) 100%, rgba($background-color, 0) 100%);
    transform: inherit;
  }

  @media (max-width: 524px) {
    width: 310px;
    height: 139px;
  }
}

.project__hidden {
  @media (max-width: 524px) {
    display: none;
  }
}
